
.box {
  height: 300px ;
  width: 200px ; 
  background: whitesmoke; 
  justify-content: space-around; 
  border: 1px black ; 
  border-radius: 30px; 
  align-items: center; 
  display: flex ; 
  flex-direction: column;
  top: 50%; 
  bottom: 50%; 
}

h2{
  border-radius: 10px ; 
}
